import zh_CN from 'antd/lib/locale-provider/zh_CN';
import moment from 'moment';

export const initStatisticItem = () => {
  let map = {};
  for (let category of [...CATEGORIES, KEY_TICKET_TOTAL]) {
    map[category] = {};
    for (let itemName of STATISTIC_ITEMS) {
      map[category][itemName] = 0;
    }
  }
  return map;
}

export const initRate = () => {
  let map = {};
  for (let category of [...CATEGORIES, KEY_TICKET_TOTAL]) {
    map[category] = {};
    for (let itemName of RATE_STATISTIC_ITEMS) {
      map[category][itemName] = 0;
    }
  }
  return map;
}

export const getCategory = ticket => ticket[COL_FIRST_CLASS] || CATEGORY_UNCLASSIFY;

export const formatStageToTable = (data) => {
  let tableData = [];
  var rowKey = 0;
  for (let name in data) {
    for (let statisticItem of STATISTIC_ITEMS) {
      let rowData = {};
      rowKey++;
      rowData.key = rowKey;
      rowData[KEY_PROJECT_NAME] = name;
      rowData[KEY_STATISTIC_ITEM] = statisticItem;
      for (let category of [...CATEGORIES, KEY_TICKET_TOTAL]) {
        rowData[category] = data[name][category][statisticItem]
      }
      tableData.push(rowData);
    }
  }
  return tableData;
}

const STAGE = /\d*/;

export function filterProjectName(name) {
  let filteredName = name;
  if (name.match(STAGE)) {
    filteredName = name.substring(0, name.match(STAGE).index);
  }
  return filteredName;
}

// 
export const statisticPeriodNewAddedTickets = (tickets, closeStartTime, closeEndTime, col) => {
  let dataMap = {};
  for (let ticket of tickets) {
    let name = ticket[col];
    if (dataMap[name] == null) {
      dataMap[name] = initStatisticItem();
    }
    let ticketCategory = getCategory(ticket);
    let ticketStatistic = dataMap[name][ticketCategory];
    let ticketStatus = ticket[COL_CURRENT_STATUS];
    let ticketReportTime = moment(ticket[COL_REPORT_TIME]);
    let ticketPauseDuration = Number(ticket[COL_PAUSE]);
    let ticketCloseTime = moment(ticket[COL_CLOSE_TIME_1] || ticket[COL_CLOSE_TIME_2] ||
      ticket[COL_CLOSE_TIME_3]);

    //  =  + 
    if (ticketStatus == TICKET_STATUS.RESPONSED || ticketStatus == TICKET_STATUS.SOLUTION_PREPARE ||
      ticketStatus == TICKET_STATUS.CLOSED || ticketStatus == TICKET_STATUS.WORK_FINISHED ||
      ticketStatus == TICKET_STATUS.DROP_IN || ticketStatus == TICKET_STATUS.FORCE_CLOSE ||
      ticketStatus == TICKET_STATUS.SOLUTION_APPROVE || ticketStatus == TICKET_STATUS.COMMENTED ||
      ticketStatus == TICKET_STATUS.WORK_DOING || ticketStatus == TICKET_STATUS.CLOSE_ABNORMAL ||
      ticketStatus == TICKET_STATUS.DISPATCHED || ticketStatus == TICKET_STATUS.NEW) {
      ticketStatistic[TICKET_ACCEPT]++;
    }

    //  = +++
    if ((ticketStatus == TICKET_STATUS.CLOSED || ticketStatus == TICKET_STATUS.FORCE_CLOSE ||
      ticketStatus == TICKET_STATUS.COMMENTED || ticketStatus == TICKET_STATUS.CLOSE_ABNORMAL) &&
      ticketCloseTime >= closeStartTime && ticketCloseTime <= closeEndTime) {
      ticketStatistic[TICKET_CLOSE]++;

      //  = 
      if (ticket[COL_OVERTIME_WARN] == null) {
        ticketStatistic[TICKET_CLOSE_ON_TIME]++;
      }

      //  = ++--
      ticketStatistic[TICKET_CLOSE_PROCESS_TIME] = ticketStatistic[TICKET_CLOSE_PROCESS_TIME] +
        moment.duration(ticketCloseTime.diff(ticketReportTime)).subtract(ticketPauseDuration, 'days').as('days');
    }

    //  = +++++++
    //    + +++
    if ((ticketStatus == TICKET_STATUS.RESPONSED || ticketStatus == TICKET_STATUS.SOLUTION_PREPARE ||
      ticketStatus == TICKET_STATUS.WORK_FINISHED || ticketStatus == TICKET_STATUS.DROP_IN ||
      ticketStatus == TICKET_STATUS.SOLUTION_APPROVE || ticketStatus == TICKET_STATUS.DISPATCHED ||
      ticketStatus == TICKET_STATUS.NEW) && ((ticketStatus == TICKET_STATUS.CLOSED || ticketStatus == TICKET_STATUS.FORCE_CLOSE ||
        ticketStatus == TICKET_STATUS.COMMENTED || ticketStatus == TICKET_STATUS.CLOSE_ABNORMAL) &&
        ticketCloseTime > closeEndTime)) {
      ticketStatistic[TICKET_OPEN]++;

      //  = --
      ticketStatistic[TICKET_OPEN_PROCESS_TIME] = ticketStatistic[TICKET_OPEN_PROCESS_TIME] +
        moment.duration(moment(closeEndTime).diff(ticketReportTime)).subtract(ticketPauseDuration, 'days').as('days');
    }

    //  = 1
    if (ticketPauseDuration > 0) {
      ticketStatistic[TICKET_PAUSE]++;
    }

    //  = 
    if (ticketStatus == TICKET_STATUS.CLOSED &&
      (ticketCloseTime >= closeStartTime && ticketCloseTime <= closeEndTime)) {
      ticketStatistic[TICKET_CLOSE_NORMAL]++;
    }

    //  = 
    if (ticketStatus == TICKET_STATUS.CLOSE_ABNORMAL &&
      (ticketCloseTime >= closeStartTime && ticketCloseTime <= closeEndTime)) {
      ticketStatistic[TICKET_CLOSE_ABNORMAL]++;
    }

    //  = +
    if (ticketStatus == TICKET_STATUS.DISPATCHED || TICKET_STATUS.NEW) {
      ticketStatistic[TICKET_NOT_RESPONSED]++;
    }

    //  = 
    if (ticketStatus == TICKET_STATUS.INVALID) {
      ticketStatistic[TICKET_INVALID]++;
    }
  }

  // 
  for (let name in dataMap) {
    for (let statisticItem of STATISTIC_ITEMS) {
      for (let category of CATEGORIES) {
        dataMap[name][KEY_TICKET_TOTAL][statisticItem] += dataMap[name][category][statisticItem];
      }
    }
  }

  // 
  for (let name in dataMap) {
    for (let category in dataMap[name]) {
      let ticketCloseCount = dataMap[name][category][TICKET_CLOSE];
      let ticketCloseProcessTime = dataMap[name][category][TICKET_CLOSE_PROCESS_TIME];
      dataMap[name][category][TICKET_CLOSE_PROCESS_TIME] =
        ticketCloseCount > 0 ? Number(ticketCloseProcessTime / ticketCloseCount).toFixed(2) : 0;

      let ticketOpenProcessTime = dataMap[name][category][TICKET_OPEN_PROCESS_TIME];
      let ticketOpenCount = dataMap[name][category][TICKET_OPEN];
      dataMap[name][category][TICKET_OPEN_PROCESS_TIME] =
        ticketOpenCount > 0 ? Number(ticketOpenProcessTime / ticketOpenCount).toFixed(2) : 0;
    }
  }
  return dataMap;
}

