export const statisticPeriodTotalResult = (array) => {
  let periodTotalResult = {};
  for (let result of array) {
    for (let name in result) {
      if (periodTotalResult[name] == null) {
        periodTotalResult[name] = initStatisticItem();
      }
      for (let category of [...CATEGORIES, KEY_TICKET_TOTAL]) {
        for (let item of STATISTIC_ITEMS) {
          if (item === TICKET_CLOSE_PROCESS_TIME) {
            let ticketCloseCount = result[name][category][TICKET_CLOSE];
            let ticketCloseProcessTime = result[name][category][TICKET_CLOSE_PROCESS_TIME];
            periodTotalResult[name][category][TICKET_CLOSE_PROCESS_TIME] +=
              ticketCloseCount * ticketCloseProcessTime;
          } else if (item === TICKET_OPEN_PROCESS_TIME) {
            let ticketOpenProcessTime = result[name][category][TICKET_OPEN_PROCESS_TIME];
            let ticketOpenCount = result[name][category][TICKET_OPEN];
            periodTotalResult[name][category][TICKET_OPEN_PROCESS_TIME] +=
              ticketOpenCount * ticketOpenProcessTime;
          }
          else {
            periodTotalResult[name][category][item] +=
              result[name][category][item];
          }
        }
      }
    }
  }
  // get average process time
  for (let name in periodTotalResult) {
    for (let category in periodTotalResult[name]) {
      let ticketCloseProcessTime = periodTotalResult[name][category][TICKET_CLOSE_PROCESS_TIME];
      let ticketCloseCount = periodTotalResult[name][category][TICKET_CLOSE];
      periodTotalResult[name][category][TICKET_CLOSE_PROCESS_TIME] =
        ticketCloseCount > 0 ? Number(ticketCloseProcessTime / ticketCloseCount).toFixed(2) : 0;

      let ticketOpenProcessTime = periodTotalResult[name][category][TICKET_OPEN_PROCESS_TIME];
      let ticketOpenCount = periodTotalResult[name][category][TICKET_OPEN];
      periodTotalResult[name][category][TICKET_OPEN_PROCESS_TIME] =
        ticketOpenCount > 0 ? Number(ticketOpenProcessTime / ticketOpenCount).toFixed(2) : 0;
    }
  }

  return periodTotalResult;
}

export const statisticRate = (statisticMap, statisticItem, rateMap) => {
  for (let name in statisticMap) {
    if (rateMap[name] == null) {
      rateMap[name] = initRate();
    }
    for (let category of [...CATEGORIES, KEY_TICKET_TOTAL]) {
      if (statisticMap[name][category][TICKET_ACCEPT] > 0) {
        if (statisticItem === NEW_ADD_CLOSE_RATE) {
          rateMap[name][category][statisticItem] =
            (statisticMap[name][category][TICKET_CLOSE] /
              statisticMap[name][category][TICKET_ACCEPT]).toFixed(4);
        } else if (statisticItem === REMAIN_CLOSE_RATE) {
          rateMap[name][category][statisticItem] =
            (statisticMap[name][category][TICKET_CLOSE] /
              statisticMap[name][category][TICKET_ACCEPT]).toFixed(4);
        } else if (statisticItem === OVERALL_CLOSE_RATE) {
          rateMap[name][category][statisticItem] =
            (statisticMap[name][category][TICKET_CLOSE] /
              statisticMap[name][category][TICKET_ACCEPT]).toFixed(4);
        } else if (statisticItem === CLOSE_ON_TIME_RATE) {
          rateMap[name][category][statisticItem] =
            (statisticMap[name][category][TICKET_CLOSE_ON_TIME] /
              statisticMap[name][category][TICKET_ACCEPT]).toFixed(4);
        } else if (statisticItem === TOTAL_OVERALL_CLOSE_RATE) {
          rateMap[name][category][statisticItem] =
            (statisticMap[name][category][TICKET_CLOSE] /
              statisticMap[name][category][TICKET_ACCEPT]).toFixed(4);
        }
      }
    }
  }
}
